.loader-container {
  display: flex;
  justify-content: center;
  align-items: center;
}

.loader {
  width: 50px;
  height: 50px;
  border-radius: 50%;
  animation: spin 1s linear infinite;
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
    box-shadow: 0 -1px 0 rgba(0, 102, 103, 0.8);
  }

  50% {
    transform: rotate(180deg);
    box-shadow: 0 -1px 0 rgba(0, 102, 103, 0.8);
  }

  100% {
    transform: rotate(360deg);
    box-shadow: 0 -1px 0 rgba(0, 102, 103, 0.8);
  }
}
